@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200&display=swap');

/* Container Styling */
.projekty-container {
  display: grid;
  grid-template-columns: repeat(2, minmax(300px, 500px)); /* Tighter max size per column */
  gap: 30px; /* Reduced gap between cards */
  justify-content: center; /* Properly centers the grid itself */
  padding: 50px 20px; /* Reduced overall padding */
  background-color: white;
}

/* Project Card */
.project-card {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.5);
  width: 100%; /* Full width inside grid cell */
}

/* Project Image */
.project-image {
  width: 100%;
  height: 50vh; /* Slightly smaller */
  object-fit: cover;
}

.project-image:hover {
  opacity: 0.7;
}

/* Overlay Info */
.project-info {
  position: absolute;
  bottom: 15px;
  left: 15px;
  background: rgba(255, 255, 255, 0.85);
  padding: 10px 14px;
  border-radius: 6px;
  width: 70%;
  height: 100px; /* Fixed height for consistency */
  color: #333;
  text-align: left;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  /* Uncomment this if you want scroll on hover instead of truncation:
  overflow-y: auto;
  */
}

.project-info h3 {
  font-size: 1rem;
  color: black;
  font-weight: bold;
  margin: 0;
}

.project-info p {
  font-size: 0.75rem;
  color: #555;
  margin-top: 5px;
  line-height: 1.4;

  display: -webkit-box;
  -webkit-line-clamp: 3; /* Max 3 lines */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.project-link {
  display: inline-block;
  margin-top: 8px;
  font-size: 0.8rem;
  color: black;
  font-weight: 600;
  text-decoration: none;
}

.project-link:hover {
  color: #333;
}

/* Responsive */
@media (max-width: 1024px) {
  .projekty-container {
    grid-template-columns: 1fr;
    padding: 30px 15px;
    gap: 20px;
  }

  .project-image {
    height: 40vh;
  }
}
