.hero-section {
  width: 100%;
  height: 30vw; /* Reduced height */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  background-color: white;
}

.hero-image {
  width: 60%; /* 70% smaller */
  height: auto;
  object-fit: cover;
  position: absolute;
  transition: opacity 4s ease-in-out, filter 4s ease-in-out, transform 4s ease-in-out;
}

/* Smooth fade transition */
.hero-image.fade {
  opacity: 0;
  filter: brightness(1.5) contrast(0.9) blur(4px);
  transform: scale(1.02);
}

.hero-image.active {
  opacity: 1;
  filter: brightness(1) contrast(1) blur(0px);
  transform: scale(1.05);
}

/* Project Slider */
.project-slider {
  display: flex;
  justify-content: center;
  margin: 40px 0;
  width: 100%;
  overflow: hidden;
}

/* Scrollable project container */
.project-container {
  margin-top: 80px;
  display: flex;
  gap: 5vw;
  overflow-x: auto;
  padding: 10px;
  scroll-behavior: smooth;
  width: 100%;
  white-space: nowrap; /* Prevent vertical scrolling */
  overscroll-behavior-x: contain; /* Prevent page scrolling while scrolling horizontally */
  scrollbar-width: none;
  padding-left: 250px;
  padding-right: 250px;
}

.project-container::-webkit-scrollbar {
  display: none; /* Hide scrollbar for Chrome/Safari */
}

.project-item {
  flex: 0 0 200px; /* Make the project cards smaller */
  text-align: center;
  transition: transform 0.5s ease-in-out;
}

.project-item img {
  width: 20vw; 
  height: auto;
  object-fit: cover;
  border-radius: 10px;
  transition: transform 0.3s ease, opacity 0.5s ease-in-out;
}

.project-item h4 {
  font-size: 0.8rem; 
  margin-top: 5px;
  color: #333;
}

.project-item:hover img {
  opacity: 0.5;
  transition: opacity 0.5s ease-in-out;
}
button{
  background-color: black;
}
a {
  text-decoration: none;
}