.top-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 80px;
  position: relative;
  z-index: 100;
  background-color: white; /* Optional: makes sure nav doesn't blend into background */
}

.logo {
  width: 12vw; /* Adjust as needed */
  max-width: 200px;
  min-width: 100px;
  height: auto;
  padding-top: 25px;
}

.nav-links {
  display: flex;
  gap: 25px;
  align-items: center;
}

.nav-link {
  font-family: 'Titillium Web', sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  color: black;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-link:hover {
  opacity: 0.5;
}
