.about-page {
    font-family: 'Titillium Web', sans-serif;
    background-color: #f8f8f8;
  }
  
  /* Hero Section */
  .hero-section {
    position: relative;
    width: 100%;
    height: 60vh;
    overflow: hidden;
  }
  
  .hero-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(0.7);
  }
  
  .hero-overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
  }
  
  .hero-overlay h1 {
    font-size: 3rem;
    margin-bottom: 10px;
  }
  
  .hero-overlay p {
    font-size: 1.2rem;
  }
  
  /* Team Section */
  .team-section {
    padding: 60px 40px;
    text-align: center;
  }
  
  .team-section h2 {
    font-size: 2rem;
    margin-bottom: 40px;
    color: #225045;
  }
  
  /* Profile Card */
  .profile-card {
    background: white;
    border-radius: 12px;
    padding: 30px 20px;
    max-width: 400px;
    text-align: center;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    flex: 1;
  }
  
  .profile-image {
    width: 100%;
    max-width: 400px;
    height: auto;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  .profile-card h3 {
    color: #11352e;
    margin-bottom: 15px;
    font-size: 1.5rem;
  }
  
  .profile-card p {
    font-size: 1rem;
    color: #555;
    line-height: 1.6;
  }
  